import React from 'react'
import { Typography, Card } from 'antd'
import ModalImage from "react-modal-image";
import {Link} from 'gatsby';
import LandingLayout from '@layouts/LandingLayout'
import redirect from '@components/redirect'
import ContextBasedRedirect from '../../../../components/redirect';

export default () => (
  <LandingLayout activeTab = '2'>
    <div className='flex flex-col items-start'>
    <Typography.Title className='text-primary-color'>
      Redirecting...
      </Typography.Title>
      <Typography.Title level={4} className='text-gray-600'>
        <ContextBasedRedirect aLink = 'https://play.google.com/store/apps/details?id=com.salehsolutions.whatsinmyfood' iLink = 'https://apps.apple.com/us/app/whatsinmyfood/id1622502571'/>
      </Typography.Title>
    </div>
  </LandingLayout>
)
